.gallery {
  @include container;
  padding-top: 108px;
  padding-bottom: 130px;

  @media (max-width: $medium-width) {
    padding-top: 74px;
    padding-bottom: 80px;
  }

  @media (max-width: $small-width) {
    padding-top: 59px;
    padding-bottom: 45px;
  }
}

.gallery__wrapper {
  display: grid;
  grid-template-columns: 1fr 1.74fr 1fr 1fr;
  column-gap: 27px;
  padding-top: 11px;

  @media (max-width: $medium-width) {
    padding-top: 0;
    column-gap: 16px;
  }

  @media (max-width: $small-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 24px;
    margin-top: -9px;
  }
}

.gallery__column {
  @include reset-list;
  display: flex;
  flex-direction: column;
}

.gallery__item {
  margin-bottom: 34px;
  height: 100%;

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: $medium-width) {
    margin-bottom: 20px;
  }

  @media (max-width: $small-width) {
    margin-bottom: 27px;

    &--mobile-hidden {
      display: none;
    }

    &:last-child {
      margin-bottom: 27px;
    }
  }
}

.gallery__column img {
  display: block;
  width: 100%;
  height: 100%;

  object-fit: cover;

  @media (max-width: $small-width) {
    // margin-bottom: 27px;
  }
}
