  font-family: "Cormorant Garamond", "Times New Roman", serif;
  font-weight: 700;
  font-size: 34px; }

.product__info-header p {
  margin: 0;
  font-size: 26px; }

.product__tabs {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-bottom: 22px; }

.product__tabs-item {
  margin-right: 35px; }
  .product__tabs-item:last-child {
    margin-right: 0; }

.product__tab {
  transition: color 0.2s ease;
  border: none;
  padding: 0;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  color: #9b9997;
  background-color: transparent;
  cursor: pointer; }
  .product__tab:hover, .product__tab:focus {
    color: #a87b62; }
  .product__tab:active {
    color: rgba(168, 123, 98, 0.3); }
  .product__tab--active {
    color: #1d1613; }

.product__details {
  margin: 0 0 50px 0; }
  @media (max-width: 767px) {
    .product__details {
      margin-bottom: 38px; } }

.product__details-item {
  display: flex;
  justify-content: space-between;
  padding: 11px 0 10px;
  border-bottom: 1px solid #f6f5f4; }

.product__details-item dt {
  font-weight: 500;
  font-size: 14px; }

.product__details-item dd {
  font-size: 14px; }

.product__buy-button {
  display: block;
  width: 100%;
  margin-bottom: 17px; }
  @media (max-width: 1023px) {
    .product__buy-button {
      margin-bottom: 13px; } }

.product__advantages {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  font-weight: 500;
  font-size: 14px;
  text-align: center;
  color: #9b9997; }
  @media (max-width: 1023px) {
    .product__advantages {
      font-size: 12px; } }
  @media (max-width: 767px) {
    .product__advantages {
      display: grid;
      grid-template-columns: repeat(3, 1fr); } }

.product__advantages-item {
  position: relative;
  flex-grow: 1; }
  .product__advantages-item::after {
    content: "";
    position: absolute;
    right: 0;
    width: 1px;
    height: 20px;
    background-color: #f6f5f4; }
  @media (max-width: 767px) {
    .product__advantages-item {
      padding: 0 15px; }
      .product__advantages-item:first-child {
        padding-left: 0; }
      .product__advantages-item:last-child {
        padding-right: 0; } }
  .product__advantages-item:last-child {
    padding-right: 0; }
    .product__advantages-item:last-child::after {
      display: none; }

.popup-add {
  padding: 0 15px; }
  .popup-add--independent-page {
    display: flex;
    position: static;
    background-color: transparent;
    padding: 100px 25px; }

.popup-add__wrapper {
  max-width: 500px;
  width: 100%;
  padding-top: 46px; }
  @media (max-width: 767px) {
    .popup-add__wrapper {
      padding-top: 38px; } }

.popup-add__wrapper h2 {
  margin-bottom: 42px;
  padding: 0 25px; }
  @media (max-width: 767px) {
    .popup-add__wrapper h2 {
      font-size: 24px;
      margin-bottom: 33px; } }

.popup-add__content {
  display: flex;
  padding: 0 35px 40px; }
  @media (max-width: 767px) {
    .popup-add__content {
      padding-left: 15px;
      padding-right: 15px; } }

.popup-add__content img {
  margin-right: 20px; }

.popup-add__details {
  flex-grow: 1; }

.popup-add__details h3 {
  margin: 0 0 9px 0;
  font-family: "Cormorant Garamond", "Times New Roman", serif;
  font-weight: 700;
  font-size: 18px; }

.popup-add__details p {
  margin: 0;
  font-size: 18px; }

.popup-add__amount-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; }

.popup-add__amount-button {
  display: flex;
  width: 14px;
  height: 14px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }

.popup-add__block {
  padding: 17px 35px 55px;
  border-top: 1px solid #f6f5f4; }

.popup-add__total-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 36px;
  font-size: 14px; }
  @media (max-width: 767px) {
    .popup-add__total-wrapper {
      flex-direction: column; } }

.popup-add__total-wrapper p {
  margin: 0; }

.popup-add__subtotal {
  font-weight: 500; }

.popup-add__actions {
  display: flex;
  column-gap: 30px; }
  @media (max-width: 767px) {
    .popup-add__actions {
      flex-direction: column; } }

.popup-add__actions-button {
  width: 100%; }
  @media (max-width: 767px) {
    .popup-add__actions-button:first-child {
      margin-bottom: 20px; } }

.popup-login {
  padding: 0 25px; }
  .popup-login--independent-page {
    display: flex;
    position: static;
    background-color: transparent;
    padding: 100px 25px; }

.popup-login__wrapper {
  position: relative;
  max-width: 430px;
  width: 100%;
  padding: 187px 45px 54px;
  background-color: #f0eae4; }
  .popup-login__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 138px;
    background-image: url("../img/sweet-ice-cream.jpg");
    background-repeat: no-repeat;
    background-size: cover; }
  @media (max-width: 767px) {
    .popup-login__wrapper {
      padding: 127px 15px 39px; }
      .popup-login__wrapper::before {
        height: 84px;
        background-size: cover; } }

.popup-login h2 {
  margin-bottom: 38px; }

.popup-login__sign-up {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9b9997;
  text-align: center; }

.popup-login__sign-up a {
  transition: color 0.2s ease;
  margin-left: 5px;
  color: #1d1613; }
  .popup-login__sign-up a:hover, .popup-login__sign-up a:focus {
    color: #a87b62; }
  .popup-login__sign-up a:active {
    color: rgba(168, 123, 98, 0.3); }

.login-form {
  margin-bottom: 15px; }

.login-form__field {
  margin-bottom: 35px; }
  .login-form__field:last-of-type {
    margin-bottom: 0; }
  @media (max-width: 767px) {
    .login-form__field {
      margin-bottom: 30px; } }

.login-form__field label {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.login-form__field input {
  width: 100%;
  padding: 0 0 9px 0;
  border: none;
  border-bottom: 1px solid #1d1613;
  background-color: transparent; }
  .login-form__field input:hover, .login-form__field input:focus {
    border-color: #a87b62; }

.login-form__forgot-wrapper {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 45px; }
  @media (max-width: 767px) {
    .login-form__forgot-wrapper {
      margin-bottom: 35px; } }

.login-form__forgot {
  transition: color 0.2s ease;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  text-align: right;
  color: #9b9997; }
  .login-form__forgot:hover, .login-form__forgot:focus {
    color: #a87b62; }
  .login-form__forgot:active {
    color: rgba(168, 123, 98, 0.3); }

.login-form button[type="submit"] {
  width: 100%; }
